}

#root {
  height: 100%;
  width: 100%;
}

.full-width-height {
  height: 100vh;
  width: 100%;
}

.container {
  width: calc(100% - 32px);
  height: calc(100% - 32px);
  padding: 16px;
}

.no-margin {
  margin: 0;
}

.display-flex {
  display: flex;
}

